<template>
  <div class="canvas_container">
    <canvas id="canvas"></canvas>
    <a-menu
      mode="horizontal"
      class="canvas-menu"
      :selectable="false"
      :multiple="true"
    >
      <a-menu-item class="menu-item">
        <span></span>
        <a-input
          v-model="width"
          @blur="penSizeChange"
          class="pen-width-input"
        />
      </a-menu-item>
      <a-menu-item key="1" class="menu-item">
        <a-button
          class="menu-btn small-width"
          id="clearCanvas"
          @click="download()"
          ><a-icon class="small-margin-right" type="download" /><span
            class="btn-text"
            ></span
          ></a-button
        >
      </a-menu-item>
      <a-menu-item key="2" class="menu-item">
        
        <a-select
          label-in-value
          :default-value="{ key: 'jpg' }"
          style="width: 70px"
          @change="imgTypeChange"
          class="img-type"
        >
          <a-select-option value="png">
            png
          </a-select-option>
          <a-select-option value="jpg">
            jpg
          </a-select-option>
          <!-- <a-select-option value="svg">
            svg
          </a-select-option> -->
        </a-select>
      </a-menu-item>

      <a-menu-item key="3" class="menu-item">
        <a-button class="menu-btn small-width" id="saveCanvas" @click="clear()"
          ><a-icon class="small-margin-right" type="delete" /><span
            class="btn-text"
            ></span
          ></a-button
        >
      </a-menu-item>
      <a-menu-item key="4" class="menu-item">
        <a-button
          @click="rePaint()"
          class="menu-btn small-width"
          :type="btnHighlight || imgFlag ? 'primary' : ''"
          ></a-button
        >
      </a-menu-item>
      <a-menu-item key="5" class="menu-item">
        <a-button
          @click="clearTrace()"
          class="menu-btn small-width"
          :type="btnHighlight || imgFlag ? 'primary' : ''"
          ></a-button
        >
      </a-menu-item>
      <a-menu-item key="6" class="menu-item">
        <a-button
          class="menu-btn small-width"
          @click="saveTraceAsJSON()"
          :type="btnHighlight ? 'primary' : ''"
          ></a-button
        >
      </a-menu-item>
      <a-menu-item key="7" class="menu-item">
        <a-button class="menu-btn small-width" @click="fileInput()"
          ></a-button
        >
        {{ file.length > 0 ? file[0].name : "none" }}
        <input id="fileInputBtn" type="file" multiple />
      </a-menu-item>
      <a-menu-item key="8" class="menu-item">
        <a-button class="menu-btn small-width" @click="imageInput()"
          ></a-button
        >
        {{ image.length > 0 ? image[0].name : "none" }}
        <input style="display:none;" id="imageInputBtn" type="file" multiple />
      </a-menu-item>
      <a-menu-item key="9" class="menu-item">
        <a-button class="menu-btn small-width" @click="traceBackWard()"
          ></a-button
        >
      </a-menu-item>
      <a-menu-item key="10" class="menu-item">
        <a-button
          :type="eraserFlag ? 'primary' : ''"
          class="menu-btn small-width"
          @click="eraserTrigger()"
          ></a-button
        >
      </a-menu-item>
      <a-menu-item key="11" class="menu-item">
        <a-button class="menu-btn small-width" @click="penShape()"
          ></a-button
        >
      </a-menu-item>
      <!-- <a-menu-item class="menu-item">
        <a-button class="menu-btn small-width" @click="findMark()"
          ></a-button
        >
      </a-menu-item> -->
      <!-- <a-menu-item class="menu-item">
        <a-button class="menu-btn small-width" @click="wdMonitor()"
          ></a-button
        >
      </a-menu-item> -->
      <!-- <a-menu-item class="menu-item">
        <a-button class="menu-btn small-width" @click="dataTest()"
          ></a-button
        >
      </a-menu-item>
      <a-menu-item class="menu-item">
        <a-button class="menu-btn small-width" @click="dataTest2()"
          ></a-button
        >
      </a-menu-item> -->
      <!-- <a-sub-menu> -->

      <!-- <a-menu-item-group key="12" class="menu-item">
          <a-menu-item>
             <span></span>
            <a-input
              @click="showMenu"
              v-model="lineWidth"
              @blur="penSizeChange"
              class="pen-width-input"
            />
          </a-menu-item>
        </a-menu-item-group> -->

      <a-menu-item key="13" class="menu-item extra-show-item">
        <span></span>
        <colorPicker
          @change="colorChange"
          class="colorPicker"
          v-model="color"
        />
      </a-menu-item>
    </a-menu>
  </div>
</template>

<script>
export default {
  // 
  props: {
    lineWidth: {
      //  5
      type: Number,
      default: 5
    },
    lineColor: {
      //  
      type: String,
      default: '#000'
    },
    canvasBgColor: {
      // 
      type: String,
      default: '#fff'
    },
    imageType: {
      // 
      type: String,
      default: 'jpg'
    }
  },
  data () {
    return {
      openKeys2: [],
      openKeys: [],
      canvas: '', // canvas
      // lineWidth: '', //  5
      // lineColor: '', // 
      // canvasBgColor: '', // 
      // imageType: 'jpg', //  jpg
      cxt: '', // canvascontext,
      trace: [], // 
      flag: '', // 
      btnHighlight: false, // 
      imgFlag: false, // 
      file: '', // 
      fileInputBtn: '', // 
      imageInputBtn: '', // 
      image: '', // 
      imageToInsert: '', // canvas
      newTrace: '', // 
      // ImageData: ''
      eraserFlag: false, // 
      eraserTrace: [], // 
      minusX: '', // x
      minusY: '', // y
      color: this.lineColor,
      width: this.lineWidth
    }
  },
  watch: {
    trace () {
      // watcher 
      if (this.trace.length > 0) {
        this.btnHighlight = true
      } else {
        this.btnHighlight = false
      }
    },
    imageToInsert () {
      if (this.imageToInsert) {
        this.imgFlag = true
      } else {
        this.imgFlag = false
      }
    }
  },
  methods: {
    // dataTest () {
    //   this.ImageData = this.cxt.getImageData(
    //     0,
    //     0,
    //     this.canvas.width,
    //     this.canvas.height
    //   )
    //   console.log(ImageData)
    // },
    // dataTest2 () {
    //   this.cxt.putImageData(this.ImageData, 0, 0)
    // },
    showMenu () {
      this.openKeys = this.openKeys2
    },
    openChange () {
      this.openKeys2 = this.openKeys
      console.log(this.openKeys)
    },
    /*  */
    injectImage (image) {
      console.log(image)
      this.imageToInsert = image
      this.drawImageOnCanvas()
    },
    /* JSON */
    injectJSON (json) {
      console.log(json)
      this.newTrace = JSON.parse(json)
      this.trace = this.trace.concat(this.newTrace)
    },
    /*  */
    mobileCompute () {
      this.minusX = this.canvas.getBoundingClientRect().x
      this.minusY = this.canvas.getBoundingClientRect().y
    },
    /* pc */
    isMobile () {
      if (
        window.navigator.userAgent.match(
          /(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i
        )
      ) {
        return true // 
      } else {
        return false // PC
      }
    },
    /*   */
    penShapeCompute () {
      var index = [] // 
      var len = []
      for (var j = 0; j < this.trace.length; j++) {
        if (this.trace[j].end) {
          index.push(j)
        }
      }
      // console.log(index)
      // 

      len[0] = index[0] + 1
      for (let j = 1; j < index.length; j++) {
        len.push(index[j] - index[j - 1])
      }

      // console.log(len)
      // var count = 0
      var decay = (this.lineWidth - 1) / len[0] // 
      // console.log(decay)
      var branchFlag = true
      // 
      for (var i = 0; i < this.trace.length; i++) {
        // console.log(this.trace[i])
        if (branchFlag) {
          this.beginAndMove(this.trace[i].x, this.trace[i].y)
          branchFlag = false
          continue
        }

        // 
        this.cxt.lineWidth -= decay
        // console.log(this.cxt.lineWidth)
        this.draw(this.trace[i].x, this.trace[i].y)
        if (this.trace[i].end) {
          branchFlag = true
          this.cxt.lineWidth = this.lineWidth
          len.shift()
          decay = (this.lineWidth - 1) / len[0]
          // count += 1
          // console.log(decay)
        }
      }
      // console.log(count)
    },
    penShape () {
      this.clearCanvas()
      this.penShapeCompute()
      this.image2Canvas(true)
    },
    /*   */
    eraserTheLine () {
      for (var i = 0; i < this.trace.length - 1; i++) {
        //  
        if (!this.trace[i].end) {
          // 
          var x1 = this.eraserTrace[0].x
          var y1 = this.eraserTrace[0].y
          var x2 = this.eraserTrace[1].x
          var y2 = this.eraserTrace[1].y
          var x3 = this.trace[i].x
          var y3 = this.trace[i].y
          var x4 = this.trace[i + 1].x
          var y4 = this.trace[i + 1].y
          var res = this.judgeIntersect(x1, y1, x2, y2, x3, y3, x4, y4)
          if (res) {
            this.removeThisLine(i)
          }
        }
      }
    },
    /*   */
    judgeIntersect (x1, y1, x2, y2, x3, y3, x4, y4) {
      // 
      // 

      // 1. abcd
      var conditon1 = Math.min(y1, y2) <= Math.max(y3, y4)
      // 2. cdab
      var condition2 = Math.min(x3, x4) < Math.max(x1, x2)
      // 3. cdab1
      var condition3 = Math.min(y3, y4) < Math.max(y1, y2)
      // 4. abcd
      var condition4 = Math.min(x1, x2) < Math.max(x3, x4)

      // 4   
      if (!(conditon1 && condition2 && condition3 && condition4)) {
        return false
      }

      // 
      //  
      // abcd cdab
      // (ACxAB)*(ABxAD) <= 0
      // AC AB <=0
      // AB AD <=0
      var u, v, w, z

      // AC AB  
      u = (x3 - x1) * (y2 - y1) - (x2 - x1) * (y3 - y1)
      // AD AB 
      v = (x4 - x1) * (y2 - y1) - (x2 - x1) * (y4 - y1)
      //  
      w = (x1 - x3) * (y4 - y3) - (x4 - x3) * (y1 - y3)
      z = (x2 - x3) * (y4 - y3) - (x4 - x3) * (y2 - y3)
      return u * v <= 0.00000001 && w * z <= 0.00000001
    },
    /*   */
    removeThisLine (index) {
      var start = 0
      var end = 0
      for (let i = index; i >= 0; i--) {
        if (this.trace[i].end) {
          start = i + 1
          break
        }
      }
      for (let i = index; i < this.trace.length; i++) {
        if (this.trace[i].end) {
          end = i
          break
        }
      }
      this.trace.splice(start, end - start + 1)
      // console.log(this.trace)
      this.rePaint()
    },
    eraserTrigger () {
      this.eraserFlag = !this.eraserFlag
      this.eraserTrace = [] // 
    },
    image2Canvas (penFlag) {
      var img = new Image()
      // console.log(this.imageToInsert)
      img.src = this.imageToInsert
      // 
      img.onload = function () {
        this.cxt.drawImage(img, 0, 0)
        // 
        if (this.trace.length > 0 && penFlag) {
          this.penShapeCompute()
        } else {
          this.rePaintTrace()
        }
      }.bind(this)
    },
    /* canvas */
    drawImageOnCanvas () {
      // this.clearCanvas()
      // this.clearTrace()
      this.image2Canvas()
    },
    /*  */
    imageInput () {
      this.imageInputBtn.click()
    },
    /* test */
    // findMark () {
    //   var em = []
    //   for (var i = 0; i < this.trace.length; i++) {
    //     if (this.trace[i].end === true) {
    //       em.push(i)
    //     }
    //   }
    //   console.log(this.trace)
    //   console.log(em)
    // },
    /*   */
    traceBackWard () {
      if (this.trace.length === 0) {
        alert('')
        return
      }
      var backFlag = true
      // this.findMark()
      // console.log(this.trace)
      // 
      for (var i = this.trace.length - 2; i >= 0; i--) {
        if (this.trace[i].end === true) {
          this.trace = this.trace.slice(0, i + 1)
          backFlag = false
          break
        }
      }
      if (backFlag) {
        this.trace = []
      }

      // this.findMark()
      // console.log(this.trace)

      this.rePaint() //  
    },
    /*  */
    fileInput () {
      this.fileInputBtn.click() // 
    },
    /*  */
    saveTraceAsJSON () {
      var content = JSON.stringify(this.trace) // JSON
      // var content = JSON.stringify(
      //   (this.ImageData = this.cxt.getImageData(
      //     0,
      //     0,
      //     this.canvas.width,
      //     this.canvas.height
      //   ))
      // )
      // console.log(content)
      var jsonLink = document.createElement('a')
      jsonLink.download = 'trace.json'
      var blob = new Blob([content]) // blob
      jsonLink.href = URL.createObjectURL(blob)
      jsonLink.click()
      this.$emit('uploadTraceJSONToFather', content)
    },
    /*  */
    eraserTraceInput (x, y) {
      this.eraserTrace.push({ x: x, y: y })
    },
    /*  */
    traceInput (x, y, flag) {
      this.trace.push({
        x: x,
        y: y,
        end: flag
      })
    },
    /*  */
    // wdMonitor () {
    //   console.log(this.canvas.width, this.canvas.height)
    //   console.log(window.innerWidth, window.innerHeight)
    // },
    /*  */
    rePaintTrace () {
      var branchFlag = true
      // 
      for (var i = 0; i < this.trace.length; i++) {
        // console.log(this.trace[i])
        if (branchFlag) {
          this.beginAndMove(this.trace[i].x, this.trace[i].y)
          branchFlag = false
          continue
        }
        if (this.trace[i].end) {
          branchFlag = true
        }
        this.draw(this.trace[i].x, this.trace[i].y)
      }
    },
    rePaint () {
      this.clearCanvas() //    

      //   
      if (this.imageToInsert) {
        this.drawImageOnCanvas()
      }
      this.rePaintTrace()
      // console.log(this.trace)
      // console.log(this.trace)
    },
    /*    */
    colorChange () {
      this.cxt.strokeStyle = this.color
      this.cxt.shadowColor = this.color
    },
    /*  number */
    penSizeChange () {
      // console.log(e) // e.data
      // console.log(typeof this.lineWidth) //
      let lineWidth = Number(this.width)
      if (lineWidth <= 0 || lineWidth > 10 || isNaN(lineWidth)) {
        //  1-10
        console.log('1-10')
        lineWidth = 5 // 
        this.cxt.lineWidth = lineWidth
        return
      }
      this.cxt.lineWidth = lineWidth // canvas
    },
    /*  */
    imgTypeChange (value) {
      // console.log(value) //{key: "jpeg", label: " jpg "}
      this.imageType = value.key
    },
    /* DOM */
    getDOMEl () {
      this.canvas = document.querySelector('#canvas')
      this.fileInputBtn = document.querySelector('#fileInputBtn')
      this.imageInputBtn = document.querySelector('#imageInputBtn')
    },
    /* canvas */
    canvasSupTest () {
      if (!(this.canvas.getContext && this.canvas.getContext('2d'))) {
        const section = document.createElement('section') // section
        section.className = 'errorCanvas'
        section.innerHTML = 'canvas'
        this.canvas.prepend(section)
        throw new Error('canvas') // 
      }
    },
    /*  */
    porpertyInit () {
      // 
      this.canvas.width = this.canvas.clientWidth
      this.canvas.height = this.canvas.clientHeight
      // this.windowToCanvas() // 
      this.cxt = this.canvas.getContext('2d')
      this.cxt.fillStyle = this.canvasBgColor // 
      this.cxt.fillRect(0, 0, this.canvas.width, this.canvas.height) //  canvas 
      this.cxt.strokeStyle = this.lineColor // 
      this.cxt.lineWidth = this.lineWidth // 
      this.cxt.lineCap = 'round' //  
      this.cxt.lineJoin = 'round' //  
      //   
      this.cxt.shadowBlur = 1
      this.cxt.shadowColor = this.lineColor
    },
    /*  */
    beginAndMove (x, y) {
      this.cxt.beginPath() // 
      this.cxt.moveTo(x, y) // 
    },
    /*  */
    draw (x, y) {
      this.cxt.lineTo(x, y)
      this.cxt.stroke()
    },
    /*  PC */
    close () {
      // this.cxt.closePath()
    },
    /*   */
    download () {
      if (this.imageType === 'jpg') {
        var img = this.canvas.toDataURL('image/jpeg', 1)
      } else if (this.imageType === 'png') {
        img = this.canvas.toDataURL('image/png')
      }

      var downLoadLink = document.createElement('a')
      downLoadLink.href = img
      downLoadLink.download = 'mySignature'
      downLoadLink.click() // 
      this.$emit('uploadImageToFather', img)
    },
    clear () {
      // this.clearTrace()
      // 
      // this.image = ''
      this.clearCanvas()
      this.image = ''
      // this.imageToInsert = ''
      this.file = ''
      this.newTrace = []
    },
    /* canvas */
    clearCanvas () {
      this.cxt.clearRect(0, 0, this.canvas.width, this.canvas.height)
      this.cxt.fillRect(0, 0, this.canvas.width, this.canvas.height) // 
    },
    /*  */
    clearTrace () {
      this.trace.splice(0, this.trace.length)
      this.imageToInsert = ''
      // this.trace = []
      // this.clearCanvas()
    },
    /* canvas()  */
    windowToCanvas () {
      this.canvas.width = window.innerWidth
      this.canvas.height = window.innerHeight
    }
  },
  components: {},
  mounted () {
    this.getDOMEl() // DOM
    this.canvasSupTest() // canvas
    this.porpertyInit() // 
    this.mobileCompute() // 
    // pc
    if (this.isMobile()) {
      /*
        bindthis
    */
      //  touchstart 
      this.canvas.addEventListener(
        'touchstart',

        function (e) {
          // console.log(e)
          // console.log(e.changedTouches[0].pageX,event.changedTouches[0].pageY)
          /*
        e.changedTouches[0].pageX X
        e.changedTouches[0].pageY Y
        */
          // console.log('start')
          if (!this.eraserFlag) {
            // console.log(e)
            // console.log(test)
            this.beginAndMove(
              e.changedTouches[0].pageX - this.minusX,
              e.changedTouches[0].pageY - this.minusY
            )
            this.traceInput(
              e.changedTouches[0].pageX - this.minusX,
              e.changedTouches[0].pageY - this.minusY,
              false
            ) // 
          }
        }.bind(this)
      )

      //  touchmove  lineTo
      this.canvas.addEventListener(
        'touchmove',
        function (e) {
          // console.log('move')

          if (!this.eraserFlag) {
            // console.log(e.changedTouches[0].pageX,event.changedTouches[0].pageY)
            this.traceInput(
              e.changedTouches[0].pageX - this.minusX,
              e.changedTouches[0].pageY - this.minusY,
              false
            ) // 
            this.draw(
              e.changedTouches[0].pageX - this.minusX,
              e.changedTouches[0].pageY - this.minusY
            )
          }
        }.bind(this)
      )

      //  
      this.canvas.addEventListener(
        'touchend',
        function (e) {
          // console.log('end')

          if (!this.eraserFlag) {
            if (this.trace.length > 0) {
              this.traceInput(
                e.changedTouches[0].pageX - this.minusX,
                e.changedTouches[0].pageY - this.minusY,
                true
              ) // 
            }
            this.draw(
              e.changedTouches[0].pageX - this.minusX,
              e.changedTouches[0].pageY - this.minusY
            )
            // this.close()
            // this.penShape() // 
          }
          // console.log('end', this.trace)
        }.bind(this)
      )
      /*  */
      document.body.addEventListener(
        'touchmove',
        function (e) {
          e.preventDefault()
        },
        { passive: false }
      )
      /*  */
      this.canvas.addEventListener(
        'touchstart',
        function (e) {
          if (this.eraserFlag) {
            if (this.eraserTrace.length === 2) {
              // 
              this.eraserTrace = []
            }
            this.eraserTraceInput(
              e.changedTouches[0].pageX - this.minusX,
              e.changedTouches[0].pageY - this.minusY
            )
            // console.log(this.eraserTrace)
          }
        }.bind(this)
      )

      this.canvas.addEventListener(
        'touchend',
        function (e) {
          // console.log(this.eraserFlag)
          if (this.eraserFlag) {
            this.eraserTraceInput(
              e.changedTouches[0].pageX - this.minusX,
              e.changedTouches[0].pageY - this.minusY
            )
            // console.log(this.eraserTrace)
            // 
            this.eraserTheLine()
            this.eraserTrace = []
          }
        }.bind(this)
      )
    } else {
      /*
      PC  bindthis
    */
      this.canvas.addEventListener(
        'mousedown',
        function (e) {
          if (!this.eraserFlag) {
            // console.log(e)
            this.traceInput(e.layerX, e.layerY, false) // 
            this.beginAndMove(e.layerX, e.layerY)
            this.flag = true // 
          }
        }.bind(this)
      )
      this.canvas.addEventListener(
        'mousemove',
        function (e) {
          if (!this.eraserFlag && this.flag) {
            this.traceInput(e.layerX, e.layerY, false) // 
            this.draw(e.layerX, e.layerY)
          }
        }.bind(this)
      )
      this.canvas.addEventListener(
        //  bug
        'mouseout',
        function (e) {
          // this.traceInput(e.pageX, e.pageY, true)
          this.flag = false // 
        }.bind(this)
      )
      this.canvas.addEventListener(
        'mouseup',
        function (e) {
          if (!this.eraserFlag) {
            if (this.trace.length > 0) {
              this.traceInput(e.layerX, e.layerY, true) // 
            }
            this.flag = false // 
            this.draw(e.layerX, e.layerY)
            // this.penShape() // 
          }

          // console.log(this.trace)
        }.bind(this)
      )

      /* PC */
      this.canvas.addEventListener(
        'mousedown',
        function (e) {
          if (this.eraserFlag) {
            if (this.eraserTrace.length === 2) {
              // 
              this.eraserTrace = []
            }
            this.eraserTraceInput(e.layerX, e.layerY)
            // console.log(this.eraserTrace)
          }
        }.bind(this)
      )

      this.canvas.addEventListener(
        'mouseup',
        function (e) {
          // console.log(this.eraserFlag)
          if (this.eraserFlag) {
            this.eraserTraceInput(e.layerX, e.layerY)
            // console.log(this.eraserTrace)
            // 
            this.eraserTheLine()
            this.eraserTrace = []
          }
        }.bind(this)
      )
    }

    /*   */
    window.addEventListener(
      'resize',
      function () {
        // location.reload()
        // alert('window resize')
        this.porpertyInit() // 
        // this.clearTrace() // 
        this.rePaint()
      }.bind(this)
    )
    /*  */
    this.fileInputBtn.addEventListener(
      'change',
      function (e) {
        this.file = e.target.files
        // console.log(this.file)
        const reader = new FileReader()
        reader.readAsText(this.file[0])
        reader.onload = function () {
          this.newTrace = JSON.parse(reader.result)
          this.trace = this.trace.concat(this.newTrace)
        }.bind(this)
      }.bind(this)
    )
    this.fileInputBtn.addEventListener('click', function (e) {
      this.value = ''
    })
    /*  */
    this.imageInputBtn.addEventListener(
      'change',
      function (e) {
        this.image = e.target.files
        const reader = new FileReader()
        reader.readAsDataURL(this.image[0])
        reader.onload = function (e) {
          this.imageToInsert = e.target.result
          this.drawImageOnCanvas()
          console.log(this.imageToInsert)
        }.bind(this)
      }.bind(this)
    )
    this.imageInputBtn.addEventListener('click', function (e) {
      this.value = ''
    })
  }
}
</script>

